import 'babel-polyfill';
import Vue from 'vue' //vue
import MintUI from 'mint-ui' //mint-ui
import VueRouter from 'vue-router' //vue
import axios from 'axios' //http
import { sync } from 'vuex-router-sync'
import store from './vuex' // vuex
import * as types from './vuex/mutation-types'
import FastClick from 'fastclick' //

import App from './App.vue' //
import routes from './route.js' //
import VueSocketio from 'vue-socket.io';
import server from './server_data';
//import Cordova from '../platforms/android/assets/www/cordova.js'
FastClick.attach(document.body);
Vue.use(VueRouter);
Vue.use(VueSocketio, server.url);
Vue.use(MintUI); //


Vue.prototype.$ajax = axios; //axiosvue
window.axios = axios;

//
window.router = new VueRouter({
    routes
});
window.AppStore = store;

let history = window.sessionStorage; //(PS:sessionStorage)
history.clear(); //
let historyCount = history.getItem('count') * 1 || 0; //count 0,  
console.log("history.getItem('count')", history.getItem('count'))
history.setItem('/', 0); //'/' 0





// ,
const commit = store.commit;

commit(types.CHECK_LOGIN_STATUS);


const pagingfn = (to, from) => {
    const toIndex = history.getItem(to.path); //
    const fromIndex = history.getItem(from.path); //



    if (toIndex) {

        // ,  
        if (toIndex > fromIndex || !fromIndex) {
            //forward,
            commit(types.UPDATE_DIRECTION, 'forward')

        } else {
            //

            commit(types.UPDATE_DIRECTION, 'reverse')
        }

    } else { //
        ++historyCount; //hfc +1 
        commit(types.UPDATE_DIRECTION, 'forward'); //vuexupdate_direction
        to.path !== '/' && history.setItem(to.path, historyCount); //,history.key,value
    }

    commit(types.MAKE_PAGE, to.name);
};
const apphao   = "1.191";
const appGuide = "appGuidennumber";
let storage = window.localStorage;
let letappGuide = storage.getItem(appGuide);

router.beforeEach((to, from, next) => {

    if (to.matched.some(record => record.meta.requireAuth)) { // 

        if (store.state.loginstart) { // vuex statetoken
            pagingfn(to, from);
            next();
        } else {
            return next({
                path: '/login'
            });
            
            //next();
        }
    } else {
        console.log("");
            //,
        if (to.matched.some(record => record.meta.loginInCheckIndex) && store.state.loginstart) {
            console.log(from);
            return next({
                path: from.fullPath
            });
        } else {

            pagingfn(to, from);
            next();
        }
    }

});



commit(types.UPDATE_DIRECTION, 'reverse')

//
router.afterEach((to, from) => {
    Vue.nextTick(() =>{
        let $layout_body = document.getElementById("layout_body");
        if($layout_body){
            $layout_body.scrollTop = 0
        }
    });
});
//,
commit(types.SAVE_ROUTER, router);


sync(store, router);

window.appVue = new Vue({
    el: '#app',
    render: h => h(App),
    router
});


